import { DruxtSearchMixin } from 'druxt-search'

export default {
  name: 'DruxtDemoSearch',

  layout: 'search',

  mixins: [DruxtSearchMixin],

  created() {
    if (typeof this.$route.query.query === 'undefined') {
      return
    }

    this.query = this.$route.query.query
  },
}
